.Projects-Grid {
  width: 100%;
  display: grid;
  grid-template: 100% / 340px 1fr;
  text-align: left; 
}

  .Projects-Left {
    padding: 10px 0;
    margin: 10px 0 0 10px;
  }
  .Projects-Right {
    padding: 20px;
  }

@media (max-width: 700px) {
  .Projects-Grid {
    grid-template: auto / 100%; 
  }
  .Projects-Left, .Projects-Right {
    padding: 15px; 
  }
  .Projects-Left {
    padding: 20px;
    margin: 0;
    min-height: 150px;
    background-color: #DDD;
  }
  .Projects-Right {
    min-height: 300px; 
  } 
}



